.checkbox-group > input {
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
  display: inline-block;
  height: 1em;
  width: 1em;
  padding: 0;
  vertical-align: middle;
  cursor: pointer;
}
.checkbox-group > input:checked {
  background-color: #51ace9;
}
.checkbox-group > input:checked:focus {
  border-color: #707579;
}
.checkbox-group > label {
  padding-left: .5em;
  cursor: pointer;
  -webkit-transition: padding-left 100ms ease-in-out;
  -o-transition: padding-left 100ms ease-in-out;
  transition: padding-left 100ms ease-in-out;
}
input:focus + label, .checkbox-group > label:hover {
  padding-left: .75em;
}



